body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

@keyframes clock {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch {
  box-sizing: content-box;
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  margin: auto;
  position: relative;
}

.stopwatch__minutes-hand {
  transform-origin: 50% 20vmin;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin: auto;
  animation: 3600s steps(60, end) infinite clock;
  position: absolute;
  inset: -20vmin 0 0;
}

.stopwatch__seconds-hand {
  transform-origin: 50% 38vmin;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin: auto;
  animation: 60s linear infinite clock;
  position: absolute;
  inset: -38vmin 0 0;
}

.stopwatch__circle {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  margin: auto;
  position: absolute;
  inset: 0;
}

.stopwatch--speed-up .stopwatch__minutes {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__seconds {
  animation-duration: 10s;
}
